 :global {
	body,
	html {
		margin: 0;
		padding: 0; }

	body {
		font-family: Arial, 'Helvetica Neue', Helvetica, sans-serif; }

	h1,
	h2,
	h3,
	h4,
	h5,
	input,
	textarea {
		font-family: inherit; }

	*,
	*::before,
	*::after {
		box-sizing: border-box; }

	button {
		cursor: pointer; } }

.wrapper {
	display: flex;
	position: fixed;
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;
	overflow: hidden;
	padding: 1em; }

.in {
	width: 100%; }



