.wrapper {
  margin: 0 -1em;
  flex-grow: 1;
  position: relative;
}

.counter {
  overflow: auto;
  overflow-scrolling: touch;
  -webkit-mask-image: linear-gradient(90deg, rgba(0, 0, 0, 0), #000000 1em, #000000 calc(100% - 1em), rgba(0, 0, 0, 0));
  padding-top: 3em;
  height: 100%;
}

.counter::-webkit-scrollbar {
  width: 0;
  height: 0;
  background: transparent;
}

.counterIn {
  display: flex;
  height: inherit;
}

.counterIn::before,
.counterIn::after {
  content: '';
  width: 7.5%;
  display: block;
  flex-shrink: 0;
}

.background {
  position: absolute;
  bottom: -4em;
  left: 0;
  right: 0;
  height: 11em;
  background-color: #F6F6F6;
}




