@import '../../common';

.wrapper {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	z-index: 1;
	display: flex;
	align-items: flex-end;
	pointer-events: none; }

.spacer,
.content {
	height: $bottom-bar-height; }

.spacer {
	margin-top: -1em; }

.content {
	position: absolute;
	left: 0;
	right: 0;
	bottom: 0;
	overflow: hidden;
	background-color: #FFFFFF;
	pointer-events: auto;
	box-shadow: 0 -2px 4px rgba(#000000, .1);
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 1em;
	transform: translateY(100%);
	opacity: 0;
	visibility: hidden;
	transition-property: transform, opacity, visibility;
	transition-duration: $bottom-bar-transition-duration;

	.isActive & {
		transform: none;
		opacity: 1;
		visibility: visible; } }



