@import '../../common';

.wrapper {
	margin: 0 -1em;
	flex-grow: 1;
	position: relative; }

.counter {
	@include horizontal-scroll;
	padding-top: 3em;
	height: 100%;

	&In {
		display: flex;
		height: inherit;

		&::before,
		&::after {
			content: '';
			width: (100% - $product-width) / 2;
			display: block;
			flex-shrink: 0; } } }

.background {
	position: absolute;
	bottom: -4em;
	left: 0;
	right: 0;
	height: $product-info-min-height + 4em;
	background-color: #F6F6F6; }



