.wrapper {
  overflow: auto;
  overflow-scrolling: touch;
  -webkit-mask-image: linear-gradient(90deg, rgba(0, 0, 0, 0), #000000 1em, #000000 calc(100% - 1em), rgba(0, 0, 0, 0));
  margin: 0 -1em;
}

.wrapper::-webkit-scrollbar {
  width: 0;
  height: 0;
  background: transparent;
}

.in {
  display: flex;
}

.item {
  position: relative;
  padding: 0 0.25em;
  display: flex;
}

.item:first-child {
  padding-left: 1em;
}

.item:last-child {
  padding-right: 1em;
}

.itemIn {
  position: relative;
  padding: .75em;
  border-radius: .3em;
  border: 1px solid #c5c5c5;
  background-color: #f0f0f0;
  color: #9b9b9b;
  display: flex;
  flex-direction: column;
  justify-content: center;
  white-space: nowrap;
  transition-property: background-color, color;
  transition-duration: .1s;
}

.isActive .itemIn {
  border-color: #128bff;
  background-color: #e0f0ff;
  color: #128bff;
}

.viewRich .itemIn {
  justify-content: flex-start;
}

.action {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
}

.title {
  font-weight: 600;
}

.note {
  font-size: .8em;
}

.title + .note {
  margin-top: .5em;
}




