:global body,
:global html {
  margin: 0;
  padding: 0;
}

:global body {
  font-family: Arial, 'Helvetica Neue', Helvetica, sans-serif;
}

:global h1,
:global h2,
:global h3,
:global h4,
:global h5,
:global input,
:global textarea {
  font-family: inherit;
}

:global *,
:global *::before,
:global *::after {
  box-sizing: border-box;
}

:global button {
  cursor: pointer;
}

.wrapper {
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  overflow: hidden;
  padding: 1em;
}

.in {
  width: 100%;
}




