.wrapper {
  padding: 1em 0;
}

.item {
  padding: .75em 0;
  display: flex;
  align-items: center;
}

.item + .item {
  border-top: 1px solid #e9e9e9;
}

.image {
  width: 2.75em;
  height: 3em;
  position: relative;
  flex-shrink: 0;
  transform-origin: center top;
}

.image.viewSmall {
  transform: scale(0.8);
}

.imageIn {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.info {
  flex-grow: 1;
  display: flex;
  flex-wrap: wrap;
  margin: 0 .5em;
}

.title {
  font-weight: 500;
  margin-right: .5em;
}

.price {
  font-size: .8em;
  position: relative;
  top: .15em;
  opacity: .6;
}

.note {
  margin-top: .4em;
  font-size: .875em;
  width: 100%;
}

.controls {
  display: flex;
  align-items: center;
}

.remove,
.add {
  color: #c5c5c5;
  border: 1px solid;
  width: 1.75em;
  height: 1.75em;
  border-radius: 50%;
  background: none;
  outline: none;
  padding: 0;
  font-size: inherit;
  transition: color .1s;
}

.remove:hover,
.add:hover {
  color: inherit;
}

.quantity {
  padding: 0 .3em;
  font-size: 1.25em;
  min-width: 2em;
  text-align: center;
}




