.wrapper {
  width: 85%;
  flex-shrink: 0;
  color: #DDDDDD;
  transition: color .1s;
  position: relative;
}

.in {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  display: flex;
  flex-direction: column;
}

.image {
  position: relative;
  flex-grow: 1;
  width: 100%;
}

.imageFrame {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.imageIn {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center bottom;
  transition: opacity .3s;
  opacity: 0;
}

.isRealVisible .imageIn.viewReal,
.isPlaceholderVisible .imageIn.viewPlaceholder {
  opacity: 1;
}

.info {
  position: relative;
  margin-top: 1em;
  padding: 1em;
  color: #000000;
  flex-shrink: 0;
  min-height: 7em;
}

.infoIn {
  position: relative;
  z-index: 1;
  display: flex;
  flex-wrap: wrap;
}

.viewShadow .name::before,
.viewShadow .price::before,
.viewShadow .note::before {
  content: '';
  height: .8em;
  background-color: currentColor;
  border-radius: .1em;
  display: inline-block;
  max-width: 100%;
}

.name,
.price,
.note {
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}

.name {
  margin: 0 .5em 0 0;
}

.name::before {
  width: 6em;
  opacity: .22;
}

.price {
  margin: 0;
  position: relative;
  top: .4em;
}

.price::before {
  width: 4em;
  opacity: .1;
}

.note {
  width: 100%;
  margin: .5em 0 0;
}

.note::before {
  width: 15em;
  opacity: .1;
}

.addToCart {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  z-index: 1;
}




