@import '../../common';

@keyframes fade-in {
	0% {
		transform: translateY(-100%);
		opacity: 0; } }

.items {
	display: flex; }

.continue {
	i {
		margin-left: .5em; } }

.action {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	border: none;
	background: none;
	outline: none; }

.item {
	position: relative;
	height: 2em;
	animation: fade-in .3s; }

.quantity {
	position: absolute;
	top: -.5em;
	right: -.4em;
	z-index: 1;
	font-size: .8em;
	min-width: 1.5em;
	text-align: center;
	color: #000000;

	.viewSmall & {
		right: -.25em; } }

.image {
	position: relative;
	height: inherit;
	width: 2em;
	transform-origin: center bottom;

	.viewSmall & {
		transform: scale($product-small-scale); }

	&In {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%; } }



